<!--  -->
<template>
  <div>
    <van-sticky>
      <van-nav-bar title="充值积分" left-arrow @click-left="onClickLeft" />
    </van-sticky>
    <div class="creditTop">
      <span class="haveCredit">可用积分 {{ topCredit | numFilter }}</span>
      <span class="tips rightEl" @click="toget">充值</span>
    </div>
    <!-- 主体内容 -->
        <div class="creditCon">
      <div class="conTitle">积分明细</div>
                                                                                                                                                                                                                <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
          <div class="conWrap" >
            <div class="creditItem" v-for="(item, index) in creditList" :key="index">
               <div class="lightEl">
                <div
                  class="itemPrice"
                >{{ item.member }}</div>
                <div class="itemTime">{{ item.currency_name }} {{item.integral}}</div>
              </div>
              <div class="rightEl itemBtm">
                <div
                  class="itemPrice"
                >{{ item.remark }}</div>
                <div class="itemTime">{{ item.create_time }}</div>
              </div>
            </div>
          </div>
    </van-list>
    </div>
  </div>
</template>
<script>
import { userCenter, integralConfig,rewardList } from "@/api/personal";
export default {
  data() {
    return {
      creditList: [],
      config: {},
           loading: false,
      finished: false,
      topCredit: 0,
          isquery: {
        page: 1,
        pageSize: 10,
        type: 4
      }
    };
  },
  created() {
         userCenter().then(res=>{
         this.topCredit=res.recharge_integral;
      })
    // this.getList();
    // integralConfig().then(res => {
    //   this.config = res;
    // });
  },
  methods: {
           onLoad() {
       rewardList(this.isquery).then(res=>{
                        this.loading = false;
                        this.isquery.page++;
                        if (res.data.length == 0) {
                            this.finished = true;
                            return;
                        }
                     this.creditList = this.creditList.concat(res.data);

                        // this.arr =resp.data;
      });
    },
      toget(){
      this.$router.push('tekmoney');
         
      },
    getList() {
      myIntegral().then(res => {
        this.topCredit = res.integral;
        this.creditList = res.data;
      });
    },
    onClickLeft() {
      this.$router.go(-1);
    }
  }
};
</script>
<style lang="less" scoped>
.creditTop {
  width: 100%;
  height: 115px;
  background: url(../../assets/imgs/personal/teamBg.png) 0 0 no-repeat;
  background-size: 100% 100%;
  padding: 25px 20px 0;
  box-sizing: border-box;
  font-size: 18px;
  color: #fff;
  .tips {
    font-size: 12px;
    width: 50px;
    text-align: center;
    height: 25px;
    line-height: 25px;
    background: #28A6FF;
    color: white;
    display: block;
  }
}
.creditCon {
  margin-top: -50px;
  background: #fff;
  width: 100%;
  height: 100%;
  border-radius: 9.5px 9.5px 0 0;
}
.conTitle {
  color: #333;
  font-size: 18px;
  padding: 12px 0 5px 17px;
}
.conWrap {
  .creditItem {
    margin: 0 15px;
    padding: 19px 0 8.5px;
    font-size: 13px;
    overflow: hidden;
    border-bottom: 1px solid #bfbfbf;
    color: #333;
    display: flex;
    justify-content: space-between;
    font-size: 13px;
    .itemBtm {
      text-align: right;
    }
    .itemTime {
      color: #999;
      font-size: 11px;
      margin-top: 15px;
    }
  }
}
.reduceNum {
  color: #fd5202;
}
</style>
